.hypnotic {
    width: 24px;
    height: 24px;
    border: 1px solid #2d2d57;
    background: repeating-conic-gradient(#0000 0 90deg,#2d2d57 0 180deg),
           repeating-conic-gradient(#2d2d57 0 90deg,#0000 0 180deg);
    background-size: 7.7px 7.7px;
    background-position: 0 0;
    animation: hypnotic-60mq8qmn 2s infinite;
    display: flex;
    justify-self: center;
 }
 
 @keyframes hypnotic-60mq8qmn {
    100% {
       background-position: 0px 15.4px,7.7px 0px;
    }
 }