body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 100vw;
    height: 100vh;
    background: black;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.btn-center {
    border: 0px;
    font-family: monospace;
    display: flex;
    align-items: center;
    gap: 5px;
    justify-content: center;
    font-size: medium;
    color: white;
    border-radius: 5px;
    width: fit-content;
    padding: 5px 20px;
    justify-self: center;
    cursor: pointer;
}

.btn-red {
    background: red;
}

.btn-blue {
    background: linear-gradient(270deg, #18eafa -39.42%, #4d0af4 197.45%);
}
textarea.classic {
    border-width: 0 !important;
    border-radius: 3px;
    background: #3b3b3b;
    font-family: Roboto Mono;
    color: white;
}

.fieldKey-icon-primary {
    background: #272b33;
    min-height: 100% !important;
    display: grid;
}

.center-content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.two-btn-end {
    display: flex;
    justify-content: end;
    gap: 15px;
}

.delete-attention {
    border-radius: 5px;
    background: red;
}


.css-i4bv87-MuiSvgIcon-root {
    font-size: 1rem!important;
}