.react-datetime-picker {
    display: inline-flex;
    position: relative;
}

.react-datetime-picker,
.react-datetime-picker *,
.react-datetime-picker *:before,
.react-datetime-picker *:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.react-datetime-picker--disabled {
    background-color: #f0f0f0;
    color: #6d6d6d;
}

/* .react-datetime-picker__wrapper {
    display: flex;
    flex-grow: 1;
    flex-shrink: 0;
    border: thin solid gray;
  } */
.react-datetime-picker__wrapper {
    display: flex;
    flex-grow: 1;
    flex-shrink: 0;
    border-width: 0px !important;
    border-radius: 0.25rem;
    background: #3b3b3b;
}

/* .react-datetime-picker__inputGroup {
    min-width: calc(4px + (4px * 3) + 0.54em * 6 + 0.217em * 2);
    flex-grow: 1;
    padding: 0 2px;
  } */
.react-datetime-picker__inputGroup {
    min-width: calc(4px + (4px * 3) + 1.54em * 6 + 0.217em * 2);
    flex-grow: 1;
    padding: 0 2px;
    display: flex;
    align-items: baseline;
}

.react-datetime-picker__inputGroup__divider {
    padding: 1px 0;
    white-space: pre;
}

.react-datetime-picker__inputGroup__divider,
.react-datetime-picker__inputGroup__leadingZero {
    display: inline-block;
}

.react-datetime-picker__inputGroup__input {
    min-width: 0.54em;
    height: calc(100% - 2px);
    position: relative;
    padding: 1px;
    border: 0;
    background: none;
    font-family: inherit;
    box-sizing: content-box;
    -webkit-appearance: textfield;
    -moz-appearance: textfield;
    appearance: textfield;
}

.react-datetime-picker__inputGroup__input::-webkit-outer-spin-button,
.react-datetime-picker__inputGroup__input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0;
}

.react-datetime-picker__inputGroup__input:invalid {
    background: rgba(255, 0, 0, 0.1);
}

.react-datetime-picker__inputGroup__input--hasLeadingZero {
    margin-left: -0.54em;
    padding-left: calc(1px + 0.54em);
}

.react-datetime-picker__inputGroup__amPm {
    font-family: inherit;
    -webkit-appearance: menulist;
    -moz-appearance: menulist;
    appearance: menulist;
}

.react-datetime-picker__button {
    border: 0;
    background: transparent;
    padding: 4px 6px;
    height: 100%;
    width: 2rem;
}

.react-datetime-picker__button:enabled {
    cursor: pointer;
}

.react-datetime-picker__button:enabled:hover .react-datetime-picker__button__icon,
.react-datetime-picker__button:enabled:focus .react-datetime-picker__button__icon {
    stroke: #0078d7;
}

.react-datetime-picker__button:disabled .react-datetime-picker__button__icon {
    stroke: #6d6d6d;
}

.react-datetime-picker__button svg {
    display: inherit;
}

.react-datetime-picker__calendar,
.react-datetime-picker__clock {
    z-index: 1;
}

.react-datetime-picker__calendar--closed,
.react-datetime-picker__clock--closed {
    display: none;
}

.react-datetime-picker__calendar {
    width: 350px;
    max-width: 100vw;
}

.react-datetime-picker__calendar .react-calendar {
    border-width: thin;
}

.react-datetime-picker__clock {
    width: 200px;
    height: 200px;
    max-width: 100vw;
    padding: 25px;
    background-color: white;
    border: thin solid #a0a096;
}

/* Datepicker */
.react-date-picker__wrapper {
    display: flex;
    flex-grow: 1;
    flex-shrink: 0;
    border: none !important;
    border-radius: 0.25rem;
    background: #3b3b3b;
}

.react-date-picker__button {
    border: 0;
    background: transparent;
    padding: 4px 6px;
    height: 100%;
    width: 2rem;
}

.react-time-picker__wrapper {
    display: flex;
    flex-grow: 1;
    flex-shrink: 0;
    border: none !important;
    border-radius: 0.25rem;
    background: #3b3b3b;
}

/* Timepicker */

/* Chrome, Safari, Edge, Opera */
.react-time-picker__inputGroup > input::-webkit-outer-spin-button,
.react-time-picker__inputGroup > input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
.react-time-picker__inputGroup > input[type='number'] {
    -moz-appearance: textfield;
}

.react-time-picker__inputGroup > input {
    background: inherit;
    border: none;
    color: black;
    /* width: 41px!important; */
}
