.popoverselector-confirm {
    min-width: 100px;
    border-radius: 0.25rem;
    text-align: center;
    color: white;
    font-family: 'Segoe UI';
    padding: 0.5rem;
}

[data-theme='dark'] .popoverselector-confirm {
    background: #1d2d4094;
}

.popoverselector-container {
    min-width: 100px;
    border-radius: 0.25rem;
    text-align: center;
    color: white;
    font-family: 'Segoe UI';
    width: fit-content;
    overflow: hidden;
}

[data-theme='dark'] .popoverselector-container {
    background: #1d2d40;
    border: 2px solid #2b415b;
}
[data-theme='light'] .popoverselector-container {
    /* background: #50C9C3;
    background: -webkit-linear-gradient(to top, #96DEDA, #50C9C3);
    background: linear-gradient(to top, #96DEDA, #50C9C3); */
    background: #00d2ff; /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #3a7bd5, #00d2ff); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #3a7bd5, #00d2ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.popoverselector-container--col {
    background: #1d2d40;
    min-width: 100px;
    border-radius: 0.25rem;
    text-align: center;
    color: white;
    display: grid;
    grid-auto-flow: column;
}

.popoverselector-container .options {
    background-color: inherit;
    margin: 0;
    padding: 0.5rem;
    font-size: small;
    display: grid;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    grid-auto-flow: column;
    font-family: 'League Spartan';
    font-weight: 400;
    font-size: small;
}

.popoverselector-container .options:hover {
    background-color: var(--hover-options_color);
}
.popoverselector-container .options:focus {
    background-color: var(--hover-options_color);
}

.popoverselector-container .options--selected {
    background: #ff00cc;
    background: -webkit-linear-gradient(to left, #333399, #ff00cc);
    background: linear-gradient(to left, #333399, #ff00cc);
}
.popoverselector-container .title {
    margin: 0;
    padding: 5px;
    font-size: small;
    text-transform: uppercase;
    font-weight: 500;
}

.popoverselector-container .subtitle {
    margin: 0;
    padding: 5px;
    font-size: small;
    text-transform: uppercase;
    font-weight: 500;
    text-align: center;
    text-transform: capitalize;
    color: cadetblue;
}

.tippy-box[data-theme~='translucent'][data-placement^='bottom'] > .tippy-arrow:before {
    border-width: 0 8px 8px !important;
    border-bottom-color: rgb(29 45 64) !important;
}
.tippy-box[data-theme~='translucent'][data-placement^='right'] > .tippy-arrow:before {
    border-right-color: rgb(29 45 64) !important;
}
.tippy-box[data-theme~='translucent'][data-placement^='left'] > .tippy-arrow:before {
    border-left-color: rgb(29 45 64) !important;
}
.tippy-box[data-theme~='translucent'][data-placement^='top'] > .tippy-arrow:before {
    border-top-color: rgb(29 45 64) !important;
}

.tippy-content {
    padding: 0px !important;
}
.tippy-arrow {
    color: #2b415b !important;
}
